import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { CardTitle, Table } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faClock } from "@fortawesome/free-solid-svg-icons";
import {
  fetchAttendance,
  clockIn,
  clockOut,
  fetchAttendanceCalculation,
} from "../action/action";
import { useState } from "react";
import { Link } from "react-router-dom";
const Breadcrumbs = () => {
  const dispatch = useDispatch();
  const tableData = useSelector((state) => state.login.attendance);
  const clockdata = useSelector((state) => state.login.isClockedIn);
  const calculation = useSelector((state) => state.login.calculation);
  console.log(calculation);
  console.log(clockdata);
  console.log("tabledata", tableData);
  useEffect(() => {
    dispatch(fetchAttendanceCalculation());
    // Retrieve the clock state from localStorage
    const storedClockedIn = localStorage.getItem("clockedIn");
    // const storedClockingTime = localStorage.getItem("clockingTime");

    if (storedClockedIn === "true") {
      setClockedIn(true);
    }

    dispatch(fetchAttendance());
  }, []);

  const [clockedIn, setClockedIn] = useState(false);
  const [clockingTime, setClockingTime] = useState("");

  const handleClockButtonClick = () => {
    if ("geolocation" in navigator) {
      // Request the user's geolocation
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const latitude = position.coords.latitude;
          const longitude = position.coords.longitude;
          // Here, you can send the latitude and longitude to your server or perform any other actions with it
          console.log("User location:", latitude, longitude);
          dispatch(clockIn(latitude, longitude));
          // Clock In functionality
          if (clockedIn) {
            dispatch(clockOut(latitude, longitude));
            setClockedIn(false);
            // Remove the clock state from localStorage on Clock Out
            localStorage.removeItem("clockedIn");
            localStorage.removeItem("clockingTime");
          } else {
            // dispatch(clockIn(latitude, longitude));
  
            setClockedIn(true);
            const currentTime = new Date().toLocaleTimeString();
            setClockingTime(currentTime);
  
            // Save the clock state to localStorage on Clock In
            localStorage.setItem("clockedIn", "true");
            localStorage.setItem("clockingTime", currentTime);
          }
        },
        (error) => {
          console.error("Error getting location:", error.message);
        }
      );
    } else {
      console.error("Geolocation is not available in this browser.");
    }
  };
  

  return (
    <>
      <div className="header">
        <div className="left-corner">
          <i className="user-icon bi bi-clock"></i>
          <div>Attendance</div>
        </div>
        <div className="tabs">
          <div className="tabs-bar">
            <div className="tabs-nav-container">
              <span className="tabs-tab-prev">
                <span className="tabs-tab-prev-icon">
                  <i className="tabs-tab-prev-icon-target"></i>
                </span>
              </span>
              <span className="tabs-tab-next">
                <span className="tabs-tab-next-icon">
                  <i className="tabs-tab-next-icon-target"></i>
                </span>
              </span>
              <div className="tabs-nav-wrap">
                <div className="tabs-nav-scroll">
                  <div className="tabs-nav">
                    <div className="tabs-tab">
                      <div className="hidden-div">
                        <Link
                          className="anchor"
                          style={{
                            lineHeight: "55px",
                            display: "inline-block",
                          }}
                        >
                          MY ATTENDANCE
                        </Link>
                      </div>
                    </div>
                    <div className="tabs-tab">
                      <div className="hidden-div">
                        <Link
                          to="/teamattendance"
                          style={{
                            lineHeight: "55px",
                            display: "inline-block",
                          }}
                        >
                          TEAM ATTENDANCE
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="myattedance" style={{ marginTop: "120px" }}>
        <div>
          <CardTitle tag="h6" className="border-bottom p-4 mt-0">
            <FontAwesomeIcon
              icon={faClock}
              spinPulse
              style={{ color: "#00cc3d" }}
            />
            <span style={{ marginLeft: "10px" }}>My Attendance</span>
          </CardTitle>
        </div>
        <div style={{ height: "40px" }}>
          <button
            className={`BaseButton-bgdhxz-0 bHeQeR Button-sc-1bi0g0l-0 ClockButton__StyledButton-sc-16k6c8y-0 hbzcOf clock-btn-db user-has-clocked-in kind-primary-default size-large ${
              clockedIn ? "is-clocked-out" : ""
            }`}
            type="button"
            onClick={handleClockButtonClick}
          >
            <div color="primary" className="ant-spin-nested-loading">
              <div className="ant-spin-container">
                <i
                  type="sign-in-alt"
                  font-size="20px !important"
                  className="Icon-s3zgp6-0 jZVCwM uil-sign-in-alt"
                ></i>
                {clockedIn ? "Clock Out" : "Clock In"}
              </div>
            </div>
          </button>
        </div>
        <div className="attendancecard">
          <div className="insidecard">
            <div className="cardds">
              <h6 className="carddstitle">Work Schedule</h6>
              <p>{calculation.workSchedule
}</p>
            </div>
            <div className="cardds">
              <h6 className="carddstitle">Logged Time</h6>
              <p>{calculation.loggedTime
}</p>
            </div>
            <div className="cardds">
              <h6 className="carddstitle">Paid Time</h6>
              <p>{calculation.paidTime}</p>
            </div>
            <div className="cardds">
              <h6 className="carddstitle">Overtime Time</h6>
              <p>{calculation.overtime}</p>
            </div>
            <div className="cardds">
              <h6 className="carddstitle">Deficit</h6>
              <p>{calculation.deficit}</p>
            </div>
          </div>
        </div>
        <Table className="no-wrap mt-3 align-middle " responsive borderless>
          <thead>
            <tr>
              <th>Date</th>
              <th>Clock In</th>
              <th>Clock In Location</th>
              <th>Clock Out</th>
              <th>Clock Out Location</th>
              <th>Work Schedule</th>
              <th>Logged time</th>
              <th>Paid time</th>
              <th>Overtime</th>
              <th>Status</th>
            </tr>
          </thead>
          <tbody>
            {tableData &&
              tableData.map((tdata, index) => (
                <tr key={index} className="border-top ">
                  <td className="mb-0  ">{tdata.date}</td>

                  <td>{tdata.clockInTime}</td>
                  <td>{tdata.clockinlocation}</td>
                  <td>{tdata.clockOutTime}</td>
                  <td className="scrollable-td">{tdata.clockoutlocation}</td>
                  <td className="scrollable-td">{tdata.workschedule}</td>
                  <td className="scrollable-td">{tdata.loggedTime}</td>
                  <td className="scrollable-td">{tdata.paidTime}</td>
                  <td className="scrollable-td">{tdata.overTime}</td>

                  <td>
                    {tdata.status === "pending" ? (
                      <span className="p-2 bg-danger rounded-circle d-inline-block ms-3"></span>
                    ) : tdata.status === "holt" ? (
                      <span className="p-2 bg-warning rounded-circle d-inline-block ms-3"></span>
                    ) : (
                      <span className="p-2 bg-success rounded-circle d-inline-block ms-3"></span>
                    )}
                  </td>
                </tr>
              ))}
          </tbody>
        </Table>
      </div>
    </>
  );
};

export default Breadcrumbs;
